import {goWindow} from '@/api/navigate.js'  // 页面跳转
export default{
	data(){
		return{
			history:[],
			hisShow:false
		}
	},
	created() {
		let that = this
		uni.getStorage({
		    key: 'searchAll_key',
		    success(res) {
				//console.log(res.data.reverse())
		        that.history = (res.data.reverse()).filter((val,index)=>{
					return res.data.indexOf(val) === index
				})
				that.hisShow = true
		    }
		});
	},
	methods:{
		clickRemove(){
			let that = this
				this.$c.showModal(
				"提示",
				"确定删除全部历史记录",
				function(){
					that.history = '';
					that.hisShow = false
					uni.removeStorage({
						key: 'searchAll_key',
					});
				}, 
				'确定',
				'取消', 
				function(){
					console.log("取消")
				})
			
		},
		handleInput(item){
			this.$store.commit("changeHistory",item)
			this.$store.commit("changeSearch",item)
			goWindow('/other_pages/SearchDetail/SearchDetail')
		}
	}
}
